export default {
	data() {
		return {
			listArr:[]
		}
	},
	onLoad(options) {
		this.getListArr()
	},
	methods: {
		pingj:function(u){
			uni.navigateTo({
				url:"../../../pj/pj?id=0&uid="+u
			})
		},
		getListArr:function(){
				let that=this;
				let usurl=that.url1+"/dinghai/order/selectOrder";
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					if(res.statusCode==200){
						that.listArr=res.data.flag
						console.log(that.listArr)
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		xiangQing:function(){
			uni.navigateTo({
				url:"../XiangQing/XiangQing"
			})
		}
	}
}
